body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.listContainer {
  width: 40%;
  margin-left: 30%;
  margin-right: 30%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }
  .listContainer .itemCard {
    padding: 5px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center; }
    .listContainer .itemCard .image {
      height: 300px; }
    .listContainer .itemCard .content {
      font-family: 'Times New Roman';
      font-size: large; }
      .listContainer .itemCard .content .link {
        cursor: pointer; }

.paralax {
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-image: url(/cs-498rk-mp2/static/media/bk.89f5c83b.jpg);
  background-size: 100% auto;
  opacity: 0.7;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

.leftButton {
  float: left; }

.movieList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; }
  .movieList .simpleImage {
    padding: 10px;
    height: 310px;
    cursor: pointer; }

.horizontalContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between; }
  .horizontalContainer .verticalM {
    margin-left: 10px !important; }
    .horizontalContainer .verticalM .itemColor {
      color: white !important; }
    .horizontalContainer .verticalM .active {
      color: #1ec2ff !important; }

